import React, { Component } from 'react';


const url = "https://news.google.com/news?q=tekken&output=rss";


class GoogleNewsList extends Component {


    constructor(props){
        super(props);
        this.state = {
            requestFailed: false
        }
    }

    //automatically call when component mount
    componentDidMount(){
        fetch(url)
            .then(response => {
                if(!response.ok){
                    console.log(response);
                    throw Error("Network request failed for Google News");                    
                }                    
                return response;
            })
            .then(d => d.text())
            .then(d =>{
                console.log("some goolge news");
                console.log(d);
            }, () => {
                console.log("nooooooooo");
                this.setState({
                    requestFailed: true
                })
            })
    }



    render() {
        return (
            <div>
                this is google news list
            </div>
        );
    }
}

export default GoogleNewsList;


